import React from "react";
import {
  BrowserRouter as Router,
  Route,
  Switch,
} from "react-router-dom";
import VideoPage from "./pages/VideoPage";
import DonatBtns from "./pages/DonationButtons";
import DonationPage from "./pages/DonationPage";

const AppRouter = () => {
  return (
    <Router>
      <Switch>
          <Route path="/dontionvideo" component={VideoPage} />
          <Route path="/dontionbtns" component={DonatBtns} />
          <Route path="/file-cdn" component={DonationPage} />
          <Route path="/" component={DonationPage} exact />
      </Switch>
    </Router>
  );
};

export default AppRouter;
